@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body {
  background-color: #fff;
  font-family: "Roboto", sans-serif;
  max-width: 100%;
  overflow-x: hidden; }

@media only screen and (max-width: 641px) {
  .container {
    max-width: 360px;
    margin: 0 0; } }

.nav-wrapper {
  opacity: 0.7; }
  .nav-wrapper .brand-logo {
    padding-top: 5px; }
  .nav-wrapper .nav-logo {
    padding: 5px;
    max-width: 200px;
    max-height: 50px; }

.bold {
  font-weight: bold; }

.font-standart {
  color: #474747; }

.font-highlight {
  text-decoration: none;
  line-height: 1;
  color: #79a7f5; }

.welcome-wrapper {
  padding: 200px 20px 100px 20px;
  font-size: 32px;
  height: 100vh; }
  @media only screen and (max-width: 768px) {
    .welcome-wrapper {
      padding: 50px 20px 100px 20px; } }
  .welcome-wrapper p {
    margin: 0; }
  .welcome-wrapper .bg {
    position: absolute;
    z-index: -1;
    top: 50px;
    right: -70px; }
    @media only screen and (max-width: 768px) {
      .welcome-wrapper .bg {
        display: none; } }

.btn {
  border-radius: 10px;
  background-color: #79a7f5 !important; }
  .btn:hover {
    background-color: #6197f3; }
  .btn:active {
    background-color: #4a88f1;
    -webkit-transform: scale(0.97);
            transform: scale(0.97);
    -webkit-transition: 0.2s;
    transition: 0.2s; }
  .btn:focus {
    background-color: #3278f0; }

.participant {
  margin: 10px 0;
  font-size: 27px; }

.arrowdown {
  cursor: pointer;
  width: 17px;
  height: auto;
  margin: 0 auto;
  margin-top: 15%; }

.about-wrapper {
  margin-top: 50px;
  color: #474747;
  font-size: 1.5em; }
  .about-wrapper h4 {
    margin-top: -10px; }
  .about-wrapper a {
    -webkit-transition: 0.3s;
    transition: 0.3s;
    color: orangered; }
    .about-wrapper a:hover {
      text-decoration: underline; }

.profile-pic {
  max-width: 230px;
  height: auto;
  margin-top: 20px;
  vertical-align: middle;
  border-radius: 2px;
  box-shadow: 2px 5px 10px 3px #949494; }

.info {
  padding-top: 10px; }
  .info p {
    margin: 5px; }

.projects .image {
  margin-top: 20px;
  position: relative; }

.projects img {
  max-width: 550px;
  border-radius: 5px;
  box-shadow: 3px 3px 15px 3px #949494; }
  @media only screen and (max-width: 738px) {
    .projects img {
      max-width: 300px; } }

.text {
  text-align: justify; }

.discription {
  margin-top: 20px; }

.options {
  margin-top: 10%; }
  .options a {
    font-size: 0.8em; }
  .options svg {
    fill: white;
    margin: -0.2em 0.3em;
    width: 1em; }

.btn-git {
  background-color: #474747 !important; }
  .btn-git:hover {
    background-color: #3a3a3a; }
  .btn-git:active {
    background-color: #2e2e2e; }
  .btn-git:focus {
    background-color: #212121; }

.projects .col {
  margin: 50px 0; }

.contacts {
  margin: 50px auto; }
  .contacts ul li {
    display: inline;
    margin: 0 1.5em;
    list-style: none; }
  .contacts svg {
    width: 4em;
    height: 4em;
    fill: black;
    border: 3px solid #79a7f5;
    padding: 0.7em 0.3em;
    border-radius: 50%; }
    .contacts svg:hover {
      fill: white;
      background-color: #79a7f5; }

.title {
  text-align: center; }

.social-nets {
  margin-top: 50px; }

